/* HexGrid.css */
.hex-grid {
    position: fixed;
    top: 110px;
    left: 50%;
    transform: translateX(-50%);
    display: grid;
    grid-template-columns: repeat(5, minmax(50px, 1fr)); /* Updated for 5 columns */
    grid-template-rows: repeat(7, minmax(50px, 1fr)); /* Updated for 7 rows */
    gap: 5px;
    max-width: 350px; /* Adjust max-width as needed */
}
  
.hex-grid-item {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
}

.hex-grid-item:hover {
    transform: scale(1.05);
}

.hex-grid-link {
    display: block;
    overflow: hidden;
    border-radius: 5px;
}

.hex-grid-image {
    width: 100%;
    height: auto;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.hex-grid-label {
    text-align: center;
    padding: 4px;
    font-size: 0.7em;
    background-color: rgba(0, 0, 0, 0.7);
    color: #ffffff;
}
